#bodyList{
background-color:#F2F2F2;
}
ul{
list-style:none;
}
/*头部过滤器样式*/
#filter{
width:960px;
margin:0 auto;
padding:0 20px 10px;
background-color:#fff;
}
#filter a{
	color:#1b1b1b;
}
a{
text-decoration:none;
color:#1b1b1b;
}
/*头部div共用样式*/
.screeconbox{
 padding: 15px 0 0px;
	position:relative;
	font-size:14px;
}
/*主题框样式*/
.screeconbox.subject{
height:85px;
padding:26px 0 5px;
}
.only{
position:absolute;
top:42px;
left:0px;
}
.clearfix{
position:absolute;
left:25px;
}
.subject>ul>li{
display:inline-block;
width: 95px;
height: 106px;
text-align: center;
}
.subject>ul>li>div{
width:50px;
height: 50px;
margin: 0 auto 5px;
}
/*类别样式*/
.category{
height:30px;
}
#clear>li{
display:inline;
}
.category>b{
position:absolute;
top:15px;
}
#clear{
margin: 0 70px 0 36px;
}
/*品牌层样式*/
#brandul>b{
position:absolute;
top:15px;
}
#brandul{
height:30px;
}
#brandul>ul>li{
display:inline;
}
#brandul>ul{
margin: 0 70px 0 36px;
}
/*搜索帮助*/
#showtolls{
padding: 13px 20px;
background: #f1f1f1;
width:1000px;
margin:0 auto;
}
.showselect{
 margin-top: 10px;
}
.showselect>li{
display:inline;
font-size:16px;
margin-left:12px;
}
.curr{
color:#DF4949;
}
/*商品图片区域*/
#product-box{
width:1000px;
margin:0 auto;
background-color:#F2F2F2;
display:flex;
justify-content:space-between;
flex-wrap: wrap;
}
.box-cont{
margin-bottom:20px;
width:235px;
height:330px;
background-color:#FFFFFF;
}
.box-cont a{
display:inline-block;
width:235px;
height:330px;
}
#product-box p{
margin:20px 5px;
font-size:14px;
font-weight:1.5;
text-align:center;
}
.box-cont p b{
display:block;
margin-top:10px;
font-size:18px;
color:#DF4949;
}
/*页码样式*/
.pages{
width:1000px;
padding:20px 0 40px 0;
margin:0 auto;
text-align:center;
overflow: hidden;
}
.pages a{
display:inline-block;
width:30px;
height:30px;
font-size:18px;
text-align:center;
line-height:30px;
border-radius:50%;
color:black;
margin-left:10px;
}
.hove{
background-color:#86E8FC;
}


