/**********导航和顶部推送样式***********/
#nav_box,#recomend{
display:flex;
justify-content:space-between;
}
#nav_box{
width:1200px;
height:360px;
margin:0 auto;
}

#head_nav li{
width:214px;
height:88px;
background:#F6F6F6;
border:1px solid #e6e6e6;
list-style-type:none;
}

/*导航隐藏内容*/
.nav_sort{
width:0;
display:none;
height:360px;
box-sizing:border-box;
position:absolute;
left:216px;
top:0px;
background:white;
z-index:100;

}
.n-show{
    width:606px;
    display:block;
    height:360px;
    transition: width 2s;
}
.nav_sort div{
width:302px;
height:360px;
box-sizing:border-box;
display:inline-block;
float:left;
border-bottom: 1px solid #e6e6e6;
border-top: 1px solid #e6e6e6;
overflow:hidden;
}
.nav_sort_img{
position:absolute;
left:303px;
}
.nav_sort div dl{
display:inline-block;
width:110px;
float:left;
margin:20px 0 0 30px;
color:#666;
font-size:14px;
}
.nav_sort div dl dt{
line-height:1;
padding:10px 0;
border-bottom: 1px solid #e6e6e6;
margin-bottom: 10px;
}
.nav_sort div dl dd{
padding: 9px 0;
line-height: 1;
}
/*导航模块*/
#head_nav div.nav_i{
padding:15px 0 0 15px;
}
#head_nav li:hover{
background:white;
border-right:1px solid white;
}
#head_nav h3{
font-size:16px;
line-height:26px;
text-align:center;
height:35px;
} 
#head_nav h3 a{
text-decoration:none;
color:#333;
margin-left:-12px;
}
#head_nav p{
margin-left:41px;
margin-top:-9px;
padding:5px 0 20px;
}
#head_nav p a{
font-size:13px;
width:60px;
color:#888;
line-height:20px;
text-decoration:none;
}
#main{
width:1200px;
margin:0 auto;
}
/*导航图*/
.nav_i{
position:relative;
}
#head_nav h3 i{
display:inline-block;
width:35px;
height:35px;
position:absolute;
top:18px;
left:18px;
}
.nav_i-1{
background:url("../img/1/nav1.jpg");
}
.nav_i-2{
background:url("../img/1/nav2.jpg");
}
.nav_i-3{
background:url("../img/1/nav3.jpg");
}
.nav_i-4{
background:url("../img/1/nav4.jpg");
}

/*商品分类样式*/
#head_nav{
width:215px;
height:360px;
position:relative;
}
#banner{
width:975px;
height:360px;
margin:0;
}
/* 顶部推送 */
#recomend{
width:1200px;
height:250px;
margin:0 auto;
margin-top:20px;
}
/*热门推荐*/
.mainbox{
    width:1200px;
    height:738px;
    margin:0 auto;
    }
    .mainbox h4{
    width:1200px;
    font-weight:400;
    font-size:28px;
    line-height:1;
    text-align:center;
    padding-bottom:15px;
    color:#333333;
    }
    .nav{
    width:1200px;
    height:22px;
    margin-bottom:35px;
    }
    #pic1,#pic2,#pic3,#pic4{
    width:1200px;
    height:580px;
    display:flex;
    justify-content:space-between;
    }
     .show_pic{
    width:280px;
    height:580px;
    }
    .show_pic2{
    width:900px;
    height:580px;
    display:flex;
    justify-content:space-between;
    align-content:space-between;
    flex-wrap:wrap;
    }
    /*热门推荐导航*/
    .mainbox dl{
    height:22px;text-align:center;
    margin:0 auto;
    padding-bottom:40px;
    
    }
    .mainbox dd{
    display:inline-block;
    font-size:16px;
    padding:0 24px;
    text-align:center;
    }
    .mainbox a{
    color:#333333;
    text-decoration:none;
    }
    .pic3{
    position:relative;
    top:0;
    left:0;
    background: #F5F5F5;
    height:285px;
    overflow: hidden;
    }
    .pic3 img{width:220px;
    transition:transform .5s linear;
        
    }
    .pic3 img:hover{
    transform:scale(1.1);
    }
    .pic3 p{
    margin:0 auto;
    position:absolute;
    top:230px;
    text-align:center;
    font-size:14px;
    width:220px;
    height:40px;
    font-weight:500;
    line-height:20px;
    }
    /*底部服务信息样式*/
#wselect-box{
    width:width;
    background:#C0C0C0;
    }
    #wselect{
    width:1253px;
    margin:0 auto;
    padding:35px 0 35px;
    }
    #wselect>ul{
    list-style:none;
    }
    #wselect>ul>li{
    display:inline-block;
    margin-right:40px;
    margin-left:40px;
    font-size:1em;
    
    }
    #wselect .sel01{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")0 0 no-repeat;
    }
    #wselect .sel02{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")-120px 0 no-repeat;
    }
    #wselect .sel03{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")-240px 0 no-repeat;
    }
    #wselect .sel04{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")-360px 0 no-repeat;
    }
    #wselect .sel05{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")-480px 0 no-repeat;
    }
    #wselect .sel06{
    width:120px;
    height:120px;
    background:url("../img/index/wselect.png")-600px 0 no-repeat;
    }
    #wselect>ul>li>h5{
    text-align:center;
    margin-top:20px;
    }
   


