#hea{
width:100%;
height:60px;
background-color:#192332;
}
.head_top{position:relative;}
.head_fixed{
position:fixed;
top:0;
left:0;
z-index:999;
}
/*菜单样式*/
#headbarnav{
width:350px;
height:60px;
margin-left:50px;
list-style:none;
padding:0;
display:inline-block;
}
#headbarnav>li{
display:inline-block;
width:80px;
text-align:center;
line-height:60px;
}
#headbarnav a{
color:#FFF;
text-decoration:none;
font-size:14px;
}
/*搜索框样式*/
#search{
width:390px;
height:39px;
border:1px solid #E0E0E0;
border-radius:20px;
display:inline-block;
background:white;
position:absolute;
top:10px;
left:600px;
}
#searchbox{
width:45px;
cursor:pointer;
line-height:38px;
text-align:center;
margin-left:20px;
font-size:16px;
font-weight:400;
}
#search input{
width:210px;
height:35px;
color:#757575;
display:inline-block;
margin-left:5px;
font-family:"微软雅黑","microsoft yahei";
border:0;

outline:none;
}
#search span{
display:inline-block;
width:20px;
line-height:38px;
text-align:center;
font-size:16px;
margin-left:10px;
}
/*搜索框按钮样式*/
.btn-s{
width:56px;
height:32px;
text-decoration:none;
position:absolute;
right:3px;
top:3px;
border-radius:20px;
}
.btn-s:hover{
background:#86E8FC;
}
#btn-search>i{
background-image:url("../img/1/search.png");
width:26px;
height:25px;
position:absolute;
left:25%;
top:10%;
}

/*登录注册样式*/

#user_div{
display:inline-block;
float:right;
width:300px;
height:60px;
margin-right:20px;
}
#btnLogin,#welcomes{
width:260px;
height:60px;
padding-left:50px;
}
.hide1{
display:none;
}
#user_div a{
color:#FFF;
text-decoration:none;
font-size:14px;
display:inline-block;
height:60px;
width:110px;
line-height:60px;
padding-left:10px;
}
#user_div span{
color:#FFF;
}
/*商品分类菜单导航*/
#nav1{
width:1200px;
height:89px;
margin:0 auto;
position:relative;
}
#menunav{
width:635px;
height:37px;
margin-left:50px;
padding-top:30px;
list-style:none;
font-size:18px;
display:inline-block;
} 
#menunav>li{
display:inline-block;
padding:0 15px;
height:37px;
line-height:37px;
text-align:center;
}
#menunav>li>a{
color:#333333;
text-decoration:none;
font-weight:600;
vertical-align:baseline;
}
.shoppingcart{
 width:180px;
 height:40px;
 display:inline-block;
 position:absolute;
 top:30px;
 left:1000px;
}
.shoppingcart>div{
display:inline-block;
position:relative;
}
.shoppingcart>span{
height:40px;
color:#333333;
line-height:40px;
font-size:18px;
vertical-align:text-bottom;
}
.shoppingcart>sup{
position:absolute;
top:6px;
left:140px;
width:13px;
display:inline-block;
font-size: 12px;
line-height: 12px;
color: #fff;
background-color: #f10215;
border-radius:13px;
text-align:center;
}



