#htm{background:#F2F2F2;}
/*商品详情div样式*/
a{
text-decoration:none;
}
#clear_header{
width:1000px;
height:567px;
margin:0 auto;
padding:20px 20px 0;
box-sizing:border-box;
display:flex;
justify-content:space-between;
background-color:#FFFFFF;
position:relative;
}

#goods-img{
width:470px;
height:547px;
}
/*中图片样式*/
#icon_list{
list-style:none;
width:420px;
margin-left:40px;
padding-top:12px;
box-sizing:border-box;
transition:all .5s linear;
}
#icon_list li{
margin-right:20px;
width:62px;
height:62px;
float:left;
}
#md{
width: 470px;
height: 470px;
border: 1px solid #ddd;
text-align: center;
position: relative;
}
#superMask{
position: absolute;
width: 470px;
height: 470px;
opacity: 0;
top: 0;
left: 0;
cursor: move;
}
#mask{
position: absolute;
width: 235px;
height: 235px;
background: #ffa;
opacity: 0.2;
top: 0;
left: 0;
display: none;
}
/*小图片样式*/
#icon_list img{
border:1px solid #BABABA;
}
#icon_list img:hover{
border:1px solid red;
}
/*放大镜图片位置样式*/
#lg_img{
position:absolute;
display:none;
top:20px;
right:20px;
border:solid 1px gray;
width:470px;
height:470px;
z-index:5;
}
/*右边详情样式*/
#goodsbasic{
width:470px;
height:449px;
padding-top:28px;
}
/*商品名称样式*/
#goodsbasic h3{
font-size:20px;
font-weight:bold;
line-height:1.4;
color:#333333;
}
/*发货时间样式*/
#goodsbasic i{
color:#df4949;
font-size:14px;
display:block;
margin:10px 0 28px;
}
/*商品价格详情div样式*/
#goods-detail{
width:462px;
height:116px;
padding:22px 14px 10px;
box-sizing:border-box;
background-color:#F6F6F6;
}
#goods-detail ul{
list-style:none;
}
#goods-detail ul li{
position:relative;
padding-left:74px;
margin-bottom:12px;
font-size:14px;
}
.price-cont label{
font-size:14px;
color:#888;
position:absolute;
left:0;
top:15px;
letter-spacing:4px;
}
.price{
  font-size: 18px;
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  color: #df4949;
  margin-right: 15px;
}
.price span{
 font-weight: bold;
font-size:32px;
line-height:34px;
}
/*会员div样式*/
.vip-box{
border-left: 1px solid #ccc;
display:inline-block;
font-size:14px;
color:#333;
}
/*服务*/
.serve label{
font-size: 14px;
color: #888;
position: absolute;
top:0;
left:0;
letter-spacing: 4px;
}
/*商品数量加减*/
.s_count{
padding-top:30px;
}
#sRegion{
padding-bottom: 12px;
position:relative;
padding-left: 70px;
}
.s_count strong{
font-size: 14px;
color: #888;
position: absolute;
left: 14px;
width: 40px;
}
.num_ctrl{
width: 100px;
height: 28px;
position: relative;
text-align: center;
display: inline-block;
overflow: hidden;
vertical-align: middle;
margin-right: 12px;
}
.num_ctrl input {
background: transparent;
display:inline-block;
border: 1px solid #e0e0e0;
width: 47px;
height: 26px;
line-height: 26px;
font-size: 14px;
padding: 0;
text-align: center;
position: absolute;
top: 0;
left: 27px;
}
.num_ctrl a{
width: 26px;
height: 26px;
display:inline-block;
border: 1px solid #e0e0e0;
position: absolute;
}
.num_ctrl a.add{
background: url("../img/1/stor.png") no-repeat 0 -125px #f4f4f4;
top: 0;
right: 0;
margin-right:0;
}
.num_ctrl a.minus{
background: url("../img/1/stor.png") no-repeat 0 -75px #f4f4f4;
top: 0;
left: 0;
margin-left:0;
}
/*立即购买*/
#buyNowBtn{
background: #df4949;
margin-left: 14px;
width: 185px;
height: 43px;
border: 1px solid #dd4a4d;
font-size: 18px;
line-height: 43px;
text-align: center;
color: #fff;
display:inline-block;
position: relative;
overflow: hidden;
vertical-align: middle;
border-radius:2em;
}
/*加入购物车*/
#addCartBtn{
border: 1px solid #df4949;
background: #FDF6F6;
color: #df4949;
width: 185px;
height: 43px;
line-height: 43px;
font-size: 18px;
margin-left: 15px;
display: inline-block;
position: relative;
overflow: hidden;
border-radius: 2em;
text-align: center;
vertical-align: middle;
}
/*分享*/
.share{
padding-left: 14px;
margin: 40px 0 20px;
}
.share label{
font-size: 14px;
color: #888;
letter-spacing: 4px;
}
.share a{
width: 30px;
height: 28px;
display: inline-block;
vertical-align: middle;
margin-right: 8px;
}
/*微信 QQ 新浪图标*/
.share a.weixin{
background:url("../img/1/share.png")1px 0 no-repeat;
}
.share a.sina{
background:url("../img/1/share.png")-38px 0 no-repeat;
}
.share a.qq{
background:url("../img/1/share.png")-114px 0 no-repeat;
}
.share a.weixin:hover{
background:url("../img/1/share.png")1px -30px no-repeat;
}
.share a.sina:hover{
background:url("../img/1/share.png")-38px -30px no-repeat;
}
.share a.qq:hover{
background:url("../img/1/share.png")-114px -30px no-repeat;
}
/*商品图片区域*/
#p_pic{
width:760px;
}
.p_pic{
width:1000px;
margin:0 auto;
}
/*菜单*/
.p_memu{
width:750px;
height:40px;
margin-top:10px;
border:solid 1px #E8E8E8;
margin-bottom:1px;
background:#F8F8F8;
box-sizing:border-box;
}
.p_memu>span{
display:inline-block;
width:100px;
line-height:36px;
font-size:16px;
color:black;
text-align:center;

box-sizing:border-box;
}
.s_click{
border-bottom:solid 2px red;
background:white;
}
/*用户评论*/
#discuss{
border:1px #E8E8E8 solid;
width:748px;
height:300px;
display:none;
}



