body{
background-color:#EFEFEF;
}

#reg{
width:1000px;
height:700px;
margin:0 auto;
background-color:#FFFFFF;
}
.m-tab{
width:330px;
margin:0 auto;
padding-top:44px;
padding-bottom:24px;
}
.left-select{
display:inline-block;
background-color:#CA5252;
color:#fff;
width:159px;
height:38px;
border:solid 1px #ccc;
line-height:38px;
text-align:center;
font-size:16px;
}
.right-select{
display:inline-block;
background-color:#CA5252;
color:#fff;
width:159px;
height:38px;
border:solid 1px #ccc;
line-height:38px;
text-align:center;
font-size:16px;
}
a{
text-decoration:none;
color:#fff;
}
.u-label{
float:left;
width:340px;
text-align:right;
margin-top:12px;
font-size:16px;
line-height:45px;
color:#333;
}
.i-input{
height:44px;
width:320px;
margin-top:12px;
border:solid 1px #ddd;
font-size:16px;
display:inline-block;
clear:both;
}
.u-input{
height:72px;
width:100%;
}
input{
padding-left:10px;
color:#BDBDBD;
font-family:"微软雅黑";
line-height:30px;
}
p{
clear:both;
}

.b-btn{
opacity:0.6;
cursor:default;
background:#ca5252;
width:332px;
height:46px;
margin-top:25px;
text-align:center;
font-size:18px;
color:#fff;
line-height:46px;
margin-left:340px;
}

.msg-default {
    color: #FF0000;
    font-size:16px;
}
.msg-error {
    color: #d00;
    font-size:16px;
}
.msg-success {
    color: #0d0;
    font-size:16px;
}
.hidden {
    display: none;
}
.true{
	position:relative;
	top:5px;
}


